import React, { useEffect, useRef, useState } from 'react'
import './App.css'
import BaseWrapper from './components/base-wrapper/base-wrapper.component'
import AdultWhiteImage from './assets/images/adult_white.png'
import QRCode from 'qrcode'
import { nricFactory } from './utils/generators'



const CrossSvg = () => {
  return (
    <svg viewBox="0 0 20 20" fill="#bbb" className="x w-6 h-6"><path data-v-1ba06274="" fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
  )
}


const App = () => {

  const canvasRef = useRef();

  const genUUIDFirst8 = () => {
    return Math.random().toString(16).substr(2, 8)
  }

  const [uuidFirst8, setUuidFirst8] = useState('6a1f467a')
  const [collectedCount, setCollectedCount] = useState(0)
  const [hideOverlay, setHideOverlay] = useState(false)

  const genNRIC = () => {
    return nricFactory.newNric()
  }

  const genQRCode = () => {
    QRCode.toCanvas(canvasRef.current, genNRIC(), {
      errorCorrectionLevel: 'Q',
      width: 233,
      height: 233,
      color: {
        light: '#0000'
      },
      version: 4
    }, (err) => {
      if (err) {
      }
    })
  }

  useEffect(() => {
    genQRCode()
  }, [])


  const handleClick = () => {
    genQRCode()
    if (collectedCount < 10) {
      setCollectedCount(c => c + 1)
    }

    // shouldn't happen but whatever
    if (collectedCount > 10) {
      setCollectedCount(10)
    }
  }

  const toggleOverlay = () => {
    setHideOverlay(b => !b)
  }

  return (
    <div>
      <div className={hideOverlay ? `hidden override` : `override`}>
        <div className="card">
          <div className="button-box">
            <button onClick={toggleOverlay}><CrossSvg /></button>
          </div>

          <div className="card-header">
            <h1> Receipt: {uuidFirst8} </h1>

            <div className="question-mark-icon">?</div>
          </div>


          <div className="content-box">
            <img src={AdultWhiteImage} />
            <h2>Adult White</h2>


            <div className="qrcode" onClick={handleClick}>
              <canvas ref={canvasRef}></canvas>
            </div>

            <p><span className="label">Price:</span> $8.00 / pack</p>
            <p><span className="label">Collected:</span> {collectedCount} / 10</p>
            <p><span className="label">Amount:</span> $80.00</p>

            <p className="instructions"> Scan QR code to collect your mask kit at <u>selected locations</u>.</p>

          </div>
        </div>
      </div >
      <BaseWrapper />
    </div >
  )
}

export default App